.popover-component {
    @include shadow();

    z-index: 1010;
    padding: 0.5rem 1rem;
    color: $theme-black;
    background-color: $theme-white;
    border-radius: $border-radius;
    font-size: $font-size-medium;
    position: fixed;

    .app-popover-content {
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
    }
}

.popover-section {
    min-width: 10rem;
}
.popover-section-title {
    font-weight: 700;
}

.popover-item-button, .popover-item-button a {
    cursor: pointer;
    display: flex;
    align-items: center;
    line-height: 2em;
    font-weight: 400;

    color: $theme-black;
    &:hover { 
        color: $theme-primary;
    }
}

.popover-section-with-icons {
    position: relative;
    padding-left: 2rem;
}

.popover-section-with-icons .popover-item-button {
    margin-left: -2rem;
    padding-left: 2rem;
    z-index: 1;
}

.popover-item-icon {
    position: absolute; left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.25rem;
    height: 2em;
}

.popover-item-divider {
    border-bottom: 2px solid $theme-gray-border;
    margin: 0.5rem 0rem;
}
.popover-item-margin {
    margin-bottom: 0.5rem;
}

.popover-item.disabled {
    cursor: default;
    opacity: 0.4;

    &.popover-item-button:hover {
        color: $theme-gray-dark;
    }
}

.popover-item-footer {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding-top: 0.5rem;

    .spacer {
        flex: 1 1 auto;
    }
}