// https://colorhunt.co/palette/157118

// Colors
$theme-white: #FFFFFF;
$theme-black: #333333;
$theme-gray-dark: red;
$theme-gray: #8a8a8a;
$theme-gray-light: #e6e6e6;
$theme-gray-lighter: #f5f5f5;
$theme-gray-lightest: #fdfdfd;
$theme-gray-border: $theme-gray-light;

$theme-primary: #33beff;

$theme-success: #42C75D;
$theme-danger: #FA3E39;
$theme-warning: #FFC200;

// Fonts and Text
$font-family: proxima-nova, "Helvetica Neue", Helvetica, Arial, sans-serif;

$font-size-small: 0.875rem;
$font-size-medium: 1rem;
$font-size-large: 1.5rem;

// Layout
$border-radius: 4px;
