.ui-widget-content {
    color: $theme-black;
    border: 1px solid $theme-gray-lighter;
    background-color: $theme-white;
}
.ui-widget-content a {
    color: $theme-primary;

    &:hover {
        color: darken($theme-primary, 10%);
    }
}
.ui-widget-overlay {
    background-color: transparentize($theme-white, 0.2);
}
.ui-widget.ui-dialog {
    background-color: $theme-white;
}
.ui-shadow {
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.14);
}
.ui-state-default {
    border: 1px solid $theme-gray-border;
    background-color: $theme-white;
}
.ui-state-disabled {
    opacity: 0.35;
}
.ui-state-disabled, .ui-state-disabled * {
    cursor: not-allowed !important;
}
.ui-corner-all {
    border-radius: $border-radius;
}

// Dialogs

.ui-widget.ui-dialog {
    font-family: $font-family;
    font-size: $font-size-medium;
    max-width: 100%;

    .ui-dialog-titlebar {
        background: none;
        padding: 1rem 1.5rem;

        .ui-dialog-title {
            font-size: 22px;
            font-weight: 700;
            text-align: left;
        }

        .ui-dialog-titlebar-icon {
            display: none;
        }

        p-header {
            display: flex;
            align-items: center;

            .title {
                flex: 1 1 auto;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }

            fa-icon {
                cursor: pointer;
                font-size: 18px;
            }
        }
    }

    .ui-dialog-content {
        padding: 1rem 1.5rem;
        max-height: 75vh;
        overflow: auto;
    }

    .ui-dialog-footer {
        border: none;
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;
        padding: 1rem 1.5rem;

        p-footer {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            min-height: 42px;

            .spacer {
                flex: 1 1 auto;
            }

            .button {
                margin: 0rem 0rem 0rem 0.325rem;
            }

            & > a {
                color: $theme-primary;
                text-decoration: none;

                &:hover {
                    color: darken($theme-primary, 10%);
                }
            }
        }

        p-footer.vertical {
            flex-direction: column !important;
        
            .button {
                margin: 0rem 0rem 0.325rem 0rem !important;
                width: 20rem;
            }
        }
    }
}

.ui-state-disabled, body .ui-widget:disabled {
    opacity: 0.4;
}

p-calendar {
    .ui-calendar {
        width: 100%;

        .ui-datepicker {
            min-width: initial;
        }

        input {
            background-color: $theme-gray-lightest;
            padding: 0.75rem 1rem;
        }
    }
    .ui-datepicker {
        border-radius: $border-radius;
        background-color: $theme-white;

        table {
            font-size: $font-size-medium;

            td > a, td > span {
                display: block;
                text-align: center;
                color: $theme-black;
                border-radius: $border-radius;
                width: 36px;
                padding: 4px;
                margin: 2px;
            }
            td > a.ui-state-active {
                color: $theme-white;
                background-color: $theme-primary;
            }
            td.ui-datepicker-today > a, td.ui-datepicker-today > span {
                background-color: $theme-gray-lighter;
            }
        }
        
    }
    .ui-datepicker:not(.ui-state-disabled) table td a:not(.ui-state-active):not(.ui-state-highlight):hover {
        background-color: $theme-gray-lighter;
    }
    .ui-timepicker a:focus {
        outline: none;
    }
}
